import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import {
  Card,
  CardBody,
  Collapse,
  Container,
  Modal,
  ModalBody,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  UncontrolledTooltip
} from 'reactstrap';
import handleNavbarTransparency from '../../helpers/handleNavbarTransparency';
import {
  authenticationRoutes,
  chatRoutes,
  componentRoutes,
  ECommerceRoutes,
  emailRoutes,
  homeRoutes,
  pageRoutes,
  pluginRoutes,
  utilityRoutes,
  widgetsRoutes
} from '../../routes';
import Login from '../auth/basic/Login';
import Registration from '../auth/basic/Registration';
import NavbarDropdown from './NavbarDropdown';
import NavbarDropdownComponents from './NavbarDropdownComponents';

const breakpoint = 'lg';

const NavbarStandard = () => {
  const [navbarCollapsed, setNavbarCollapsed] = useState(true);
  const [showRegistrationModal, setShowRegistrationModal] = useState(false);

  const components = [componentRoutes, pluginRoutes, utilityRoutes];
  const pages = [pageRoutes, emailRoutes, ECommerceRoutes, widgetsRoutes, chatRoutes];

  useEffect(() => {
    window.addEventListener('scroll', handleNavbarTransparency);
    return () => window.removeEventListener('scroll', handleNavbarTransparency);
  }, []);

  return (
    <Navbar
      dark
      fixed="top"
      expand={breakpoint}
      className={classNames('navbar-standard navbar-theme', {
        'bg-dark': !navbarCollapsed
      })}
    >
      <Container>
        <NavbarBrand className="text-white" tag={Link} to="/">
          Falcon
        </NavbarBrand>
        <NavbarToggler onClick={() => setNavbarCollapsed(!navbarCollapsed)} />
        <Collapse isOpen={!navbarCollapsed} navbar>
          <Nav navbar>
            <NavbarDropdown title={homeRoutes.name} items={homeRoutes.children} />
            <NavbarDropdown title={pageRoutes.name} items={pages} />
            <NavbarDropdownComponents title={componentRoutes.name} items={components} />
            <NavbarDropdown title={authenticationRoutes.name} items={authenticationRoutes.children} />

            <NavItem>
              <NavLink tag={Link} to="/documentation">
                Documentation
              </NavLink>
            </NavItem>
          </Nav>
          <Nav navbar className="ml-auto">
            <NavItem>
              <NavLink tag={Link} to="/">
                <FontAwesomeIcon
                  icon="chart-pie"
                  id="dashboardTooltip"
                  className={`d-none d-${breakpoint}-inline-block`}
                />
                <UncontrolledTooltip placement="bottom" target="dashboardTooltip">
                  Dashboard
                </UncontrolledTooltip>
                <span className={`d-${breakpoint}-none`}>Dashboard</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/documentation">
                <FontAwesomeIcon
                  icon="book"
                  id="documentationTooltip"
                  className={`d-none d-${breakpoint}-inline-block`}
                />
                <UncontrolledTooltip placement="bottom" target="documentationTooltip">
                  Documentation
                </UncontrolledTooltip>
                <span className={`d-${breakpoint}-none`}>Documentation</span>
              </NavLink>
            </NavItem>
            <NavbarDropdown title="Login" right>
              <Card className="navbar-card-login shadow-none">
                <CardBody className="fs--1 font-weight-normal p-4">
                  <Login />
                </CardBody>
              </Card>
            </NavbarDropdown>
            <NavItem>
              <NavLink tag={Link} to="#!" onClick={() => setShowRegistrationModal(!showRegistrationModal)}>
                Register
              </NavLink>
              <Modal
                isOpen={showRegistrationModal}
                centered
                toggle={() => setShowRegistrationModal(!showRegistrationModal)}
              >
                <ModalBody className="p-0">
                  <Card>
                    <CardBody className="fs--1 font-weight-normal p-4">
                      <Registration />
                    </CardBody>
                  </Card>
                </ModalBody>
              </Modal>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
};

export default NavbarStandard;
